import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';


window.addEventListener('scroll', function(e) {
    setTimeout(console.log(1), 300)
})

function isVisible(elem) {
    let coords = elem.getBoundingClientRect();
    let windowHeight = document.documentElement.clientHeight;
    let topVisible = coords.top > 0 && coords.top < windowHeight;
    let bottomVisible = coords.bottom < windowHeight && coords.bottom > 0;
    return topVisible || bottomVisible;
} 

function getVisibleIndex() {
    var reader = document.getElementsByClassName( 'reader__body' )[0];
    var paragraphs = reader.getElementsByTagName( 'p' );
    for ( var i = 0; i < paragraphs.length; i++ ) {
        console.log(i)
    if ( isVisible( paragraphs[i] ) ) return i;
    }
    return -1;
    }



    class Reader extends React.Component {
        constructor(props) {
            super(props);
        }
        
    componentDidMount() { 
         getVisibleIndex() 
        }

        render() {
            return (
                <Panel id={this.props.id}>
                <header></header>
                <div className="titlesbiography"  onClick={() => this.props.this.goBack()}>
                    <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> 
                </div>
                        <div className="hard">
            <div className="test">
                <div dangerouslySetInnerHTML={{__html: this.props.this.state.booktext}} />
                {/* {setTimeout( getVisibleIndex(), 10000 )} */}
            </div>
            </div>
            {this.props.this.state.is_rated === false ? 
                <Button  style={{backgroundColor: "#00cbbe"}} stretched onClick={() => this.props.this.setState({activeModal: "modals", commentforrezention_add: ""})}></Button>
                :null}
            </Panel>
            )
            }
        }
  
export default Reader;